import React, { useContext, useEffect, useRef, useState } from "react";
import "react-toastify/dist/ReactToastify.css";
import Marquee from "react-fast-marquee";
import Counter from "./Components/Counter";
import { FadeLoader } from "react-spinners";
import { audioPlayer, checkIfAxios, convertTo100Percent, disableKeyboardMouse, placeBet } from "../Globals/GlobalFunctions";
import { DataContext } from "../Context/DataContext";
import { BetOpperation } from "./Components/BetOpperation";
import { ToastContainer, toast } from "react-toastify";
import { UserOpperation } from "./Components/UserOpperation";
import { toastConfig } from "../Globals/GlobalMetaData";
import ImageSlider from "./Components/ImageSlider";
import { useNavigate } from "react-router-dom";

function Spin2Win() {
  const {
    userName,
    userBalance,
    setUserBalance,
    gameId,
    setGameId,
    setTicketData,
    setLastBetBarCode,
    isMultiplierActive,
  } = useContext(DataContext);
  const {
    selectedChip,
    setSelectedChip,
    totalBetArr,
    setTotalBetArr,
    totalBetAmount,
    handleSelectBet,
    handleDoubleBet,
    handleOddEvenSelect,
    handleClearAllValues,
  } = BetOpperation();
  const {
    time,
    gameTime,
    setGameTime,
    isTimerActive,
    setIsTimerActive,
    spinSpeed,
    isSpin,
    currentGameResult,
    gameResult,
    canBet,
    setCanBet,
    printCheckboxRef,
    multiBetRef,
    resultWheelRef,
    setIsTicketType,
    updateUserData,
    handleTicketPrint,
    handleUtilityToggle,
    updateGameTime,
    handlePreTimerReset,
    handleTimerReset,
    handleCurrentGameResult,
    handleGameResults,
    setCanLastReceipt
  } = UserOpperation();


  const [isLoading, setIsLoading] = useState(true);
  const navigate = useNavigate();

  // Loader
  useEffect(() => {
    setTimeout(() => {
      setIsLoading(false)
    }, 1000)
  })

  // Functions called on Init
  useEffect(() => {
    handleClearAllValues();
    updateUserData();
    handleGameResults();
    audioPlayer(1, "PlaceYourBets");
    toast.info("Place Your Bets", toastConfig);
  }, []);

  // Timer Switch
  useEffect(() => {
    updateGameTime();
  }, [isTimerActive]);

  // Timer Logic
  useEffect(() => {
    let intervalId;
    if (isTimerActive) {
      intervalId = setInterval(() => {
        setGameTime((prevTime) => {
          if (prevTime <= 1) {
            clearInterval(intervalId);
            setIsTimerActive(false);
            setCanBet(false);
            handlePreTimerReset("spin2win");
            setTimeout(() => {
              handleTimerReset();
            }, 12000);
            // console.log("Stage 1 Done");
            return 0;
          } else if (prevTime <= 10) {
            disableKeyboardMouse(true);
          }
          return Math.max(prevTime - 1, 0);
        });
      }, 1000);
    } else {
      clearInterval(intervalId);
    }
    return () => clearInterval(intervalId);
  }, [isTimerActive]);

  // Get Last Game Result And Set the Wheel to it.
  useEffect(() => {
    if (gameResult.length > 0) {
      const lastGameResult = gameResult.filter((ele) => parseInt(ele.id) === (parseInt(gameId) - 1))
      if (lastGameResult) {
        if (lastGameResult.substring(1) === "0") {
          resultWheelRef.current.style = "transform: rotate(252deg)"
        } else if (lastGameResult.substring(1) === "1") {
          resultWheelRef.current.style = "transform: rotate(216deg)"
        } else if (lastGameResult.substring(1) === "2") {
          resultWheelRef.current.style = "transform: rotate(180deg)"
        } else if (lastGameResult.substring(1) === "3") {
          resultWheelRef.current.style = "transform: rotate(144deg)"
        } else if (lastGameResult.substring(1) === "4") {
          resultWheelRef.current.style = "transform: rotate(108deg)"
        } else if (lastGameResult.substring(1) === "5") {
          resultWheelRef.current.style = "transform: rotate(72deg)"
        } else if (lastGameResult.substring(1) === "6") {
          resultWheelRef.current.style = "transform: rotate(36deg)"
        } else if (lastGameResult.substring(1) === "7") {
          resultWheelRef.current.style = "transform: rotate(0deg)"
        } else if (lastGameResult.substring(1) === "8") {
          resultWheelRef.current.style = "transform: rotate(324deg)"
        } else if (lastGameResult.substring(1) === "9") {
          resultWheelRef.current.style = "transform: rotate(288deg)"
        }
      }
    } else {
      return
    }
  }, [])

  // Bet Place Function
  async function handleBetPlacement() {
    setIsTicketType("BET");
    setTicketData(null);
    if (canBet) {
      setCanBet(false);
      if (
        totalBetAmount > 0 &&
        gameTime > 10 &&
        userBalance >= totalBetAmount
      ) {
        const toastId = toast.loading("Placing Bet, Please Wait!", toastConfig);
        const data = await placeBet(
          userName,
          totalBetAmount,
          gameId,
          totalBetArr
        );
        if (data && data.message && data.status) {
          handleClearAllValues();
          setLastBetBarCode(data.barcode);
          toast.update(toastId, {
            render: "Bet Placed Successfully!",
            type: "success",
            isLoading: false,
            autoClose: 2000,
          });
          // setUserBalance(parseInt(userBalance) - parseInt(totalBet));
          updateUserData();
          handleTicketPrint(data.barcode);
          // setCanCancelBet(true);
          setCanLastReceipt(true);
          // setIsDuplicateTicket(false);
        } else {
          toast.update(toastId, {
            render: `Bet Failed! ${data.message}`,
            type: "error",
            isLoading: false,
            autoClose: 2000,
          });
          handleClearAllValues();
          setCanBet(true);
        }
      } else if (gameTime < 10) {
        toast.error("No Time Left!", toastConfig);
        handleClearAllValues();
        setCanBet(false);
      } else if (totalBetAmount === 0) {
        toast.error("Please Input Amount", toastConfig);
        setCanBet(true);
      } else if (userBalance < totalBetAmount) {
        toast.error("Not enough balance!", toastConfig);
        setCanBet(true);
      }
    }
  }

  useEffect(() => {
    console.log(totalBetArr);
    console.log(totalBetAmount);
  }, [totalBetArr]);

  const wheelRingImages = [
    { url: require("../Assets/images/spin2win/wheelRing2.png") },
    { url: require("../Assets/images/spin2win/wheelRing3.png") },
    { url: require("../Assets/images/spin2win/wheelRing4.png") },
  ];
  const wheelMultiImages = [
    { url: require("../Assets/images/spin2win/wheelMulti1.png") },
    { url: require("../Assets/images/spin2win/wheelMulti2.png") },
    { url: require("../Assets/images/spin2win/wheelMulti3.png") },
    { url: require("../Assets/images/spin2win/wheelMulti4.png") },
  ];

  return isLoading ? (
    <div className="loader" >
      <FadeLoader color="#454545" />
    </div>
  ) : (
    <div className="spin2win gameCanvas">
      <div className="gameHeader">
        <img
          src={require("../Assets/images/spin2win/header.png")}
          className="elementBgImg"
        />
        <div className="col-2 ms-4 me-auto" style={{ height: "max-content" }}>
          <div className="simpleOptions">
            {/* <div className='position-relative z-1'> */}
            <section className="customCheckbox container">
              <label>
                <input
                  name="check"
                  type="checkbox"
                  ref={printCheckboxRef}
                  onChange={() =>
                    handleUtilityToggle(printCheckboxRef, "Ticket Printing")
                  }
                />
                <span></span>
                <p>
                  <i className="fa-solid fa-receipt"></i>
                </p>
              </label>
            </section>
            <section className="customCheckbox container">
              <label>
                <input
                  name="check"
                  type="checkbox"
                  ref={multiBetRef}
                  onChange={() =>
                    handleUtilityToggle(multiBetRef, "Multiple Bets")
                  }
                />
                <span></span>
                <p>
                  <i className="fa-solid fa-users"></i>
                </p>
              </label>
            </section>

            {/* <div style={{ height: '21px' }}>
                                <input type='checkbox' ref={printCheckboxRef} />
                                <label className='ms-2'>Auto Print Ticket</label>
                            </div>
                            <div>
                                <input type='checkbox' ref={multiBetRef} />
                                <label className='ms-2'>Multiple Bets</label>
                            </div> */}
            {/* </div> */}
            {/* <img src={require('../Assets/images/spin2win/resultpane.png')} className='elementBgImg' /> */}
          </div>
        </div>
        <div className="col-auto" style={{ height: "max-content" }}>
          <div className="accHolder">
            <label className="point">{userName}</label>
            <div className="pointsImgWrap">
              <img
                src={require("../Assets/images/spin2win/resultpane.png")}
                className="elementBgImg"
              />
            </div>
          </div>
        </div>
        <div className="col-2 ms-4" style={{ height: "max-content" }}>
          <div className="pointsHolder">
            <label>POINTS:</label>
            <label className="point">
              <Counter number={userBalance} />
            </label>
            <div className="pointsImgWrap">
              <img
                src={require("../Assets/images/spin2win/resultpane.png")}
                className="elementBgImg"
              />
            </div>
          </div>
        </div>
        <div className="col-3 mx-4">
          <div className="resultPane">
            <ul>
              {gameResult?.map((item, index) => (
                <li key={index}>
                  <span className="result">{item.result.substring(1)}</span>
                  <span className="gameId">{item.bettime.spilt[1].substr(-7)}</span>
                </li>
              ))}
              {/* <li>
                <span className="result">1</span>
                <span className="gameId">9:00</span>
              </li>
              <li>
                <span className="result">2</span>
                <span className="gameId">9:05</span>
              </li>
              <li>
                <span className="result">3</span>
                <span className="gameId">9:10</span>
              </li>
              <li>
                <span className="result">4</span>
                <span className="gameId">9:20</span>
              </li>
              <li>
                <span className="result">5</span>
                <span className="gameId">9:25</span>
              </li>
              <li>
                <span className="result">6</span>
                <span className="gameId">9:30</span>
              </li>
              <li>
                <span className="result">7</span>
                <span className="gameId">9:35</span>
              </li>
              <li>
                <span className="result">8</span>
                <span className="gameId">9:40</span>
              </li>
              <li>
                <span className="result">9</span>
                <span className="gameId">9:45</span>
              </li>
              <li>
                <span className="result">0</span>
                <span className="gameId">9:50</span>
              </li> */}
            </ul>
            {/* <img src={require('../Assets/images/spin2win/resultpane.png')} className='elementBgImg' /> */}
          </div>
        </div>
        <div className="col-1">
          <div className="controlBtns">
            <button className="me-2" onClick={() => handleCurrentGameResult("spin2win")}>
              <img
                src={require("../Assets/images/system/infobtnRed.png")}
                className="elementBgImg"
              />
            </button>
            <button onClick={() => navigate(-1)}>
              <img
                src={require("../Assets/images/system/closebtnRed.png")}
                className="elementBgImg"
              />
            </button>
          </div>
        </div>
      </div>
      <div className="gameBody">
        <div className="px-5 container-fluid">
          <div className="row">
            <div className="col-6">
              <div className="wheelWrapper">
                <div className="resultWheel">
                  {isMultiplierActive ? (
                    <Marquee
                      className="wheelMultiplier"
                      play={true}
                      autoFill={true}
                      speed={500}
                    >
                      <label>N</label>
                      <label>1</label>
                      <label>2</label>
                      <label>3</label>
                      <label>4</label>
                      <label>5</label>
                      <label>10</label>
                    </Marquee>
                  ) : (
                    ""
                  )}
                  <img
                    ref={resultWheelRef}
                    src={require("../Assets/images/spin2win/resultWheel.png")}
                    className="resultWheel"
                  // style={{ animation: isSpin ? `spin ${spinSpeed}s linear infinite` : "" }}
                  />
                  {isMultiplierActive ? (
                    <ImageSlider
                      images={wheelMultiImages}
                      className={"wheelMulti"}
                      transTime={500}
                    />
                  ) : (
                    ""
                  )}
                  <ImageSlider
                    images={wheelRingImages}
                    className={"wheelRing"}
                    transTime={500}
                  />
                  {/* <img src={require('../Assets/images/spin2win/wheelRing.png')} className='wheelRing' /> */}
                  <div className="wheelBase">
                    <div className="wheelLabels">
                      <div className="col-5">
                        <label>PLAY:</label>
                        <label className="ms-2">{totalBetAmount}</label>
                      </div>
                      <div className="col-5">
                        <label className="text-success">WIN:</label>
                        <label className="ms-2 text-success">0</label>
                      </div>
                    </div>
                    <img
                      src={require("../Assets/images/spin2win/wheelBase.png")}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6">
              <div className="betWrapper">
                <div className="row">
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N1")}
                    >
                      <label className="amount">{totalBetArr["N1"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack1.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N2")}
                    >
                      <label className="amount">{totalBetArr["N2"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack2.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N3")}
                    >
                      <label className="amount">{totalBetArr["N3"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack3.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N4")}
                    >
                      <label className="amount">{totalBetArr["N4"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack4.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N5")}
                    >
                      <label className="amount">{totalBetArr["N5"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack5.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N6")}
                    >
                      <label className="amount">{totalBetArr["N6"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack6.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N7")}
                    >
                      <label className="amount">{totalBetArr["N7"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack7.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N8")}
                    >
                      <label className="amount">{totalBetArr["N8"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack8.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N9")}
                    >
                      <label className="amount">{totalBetArr["N9"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack9.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className="betBtn"
                      onClick={() => handleSelectBet("N0")}
                    >
                      <label className="amount">{totalBetArr["N0"]}</label>
                      <img
                        src={require("../Assets/images/spin2win/betBtnStack0.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                </div>
              </div>
              <div className="chipWrapper">
                <div className="row">
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 1 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(1)}
                    >
                      <label className="amount">1</label>
                      <img
                        src={require("../Assets/images/system/chip1.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 2 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(2)}
                    >
                      <label className="amount">2</label>
                      <img
                        src={require("../Assets/images/system/chip2.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 5 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(5)}
                    >
                      <label className="amount">5</label>
                      <img
                        src={require("../Assets/images/system/chip3.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 10 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(10)}
                    >
                      <label className="amount">10</label>
                      <img
                        src={require("../Assets/images/system/chip4.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 50 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(50)}
                    >
                      <label className="amount">50</label>
                      <img
                        src={require("../Assets/images/system/chip5.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                  <div className="col-2">
                    <button
                      className={
                        selectedChip === 100 ? "chipBtn selected" : "chipBtn"
                      }
                      onClick={() => setSelectedChip(100)}
                    >
                      <label className="amount">100</label>
                      <img
                        src={require("../Assets/images/system/chip6.png")}
                        className="elementBgImg"
                      />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="gameFooter">
        <img
          src={require("../Assets/images/spin2win/footer.png")}
          className="elementBgImg"
        />
        <div className="h-100 px-5 container-fluid">
          <div className="row col-8 ms-auto align-items-center h-100">
            <div className="col-8">
              <div className="oppBtnWrapper">
                <button
                  className="oppBtn"
                  onClick={() => handleOddEvenSelect("odd")}
                >
                  <label>Odd</label>
                  <img
                    src={require("../Assets/images/spin2win/oppBtn.png")}
                    className="elementBgImg"
                  />
                </button>
                <button
                  className="oppBtn"
                  onClick={() => handleOddEvenSelect("even")}
                >
                  <label>Even</label>
                  <img
                    src={require("../Assets/images/spin2win/oppBtn.png")}
                    className="elementBgImg"
                  />
                </button>
                <br />
                <button className="oppBtn" onClick={handleDoubleBet}>
                  <label>Double</label>
                  <img
                    src={require("../Assets/images/spin2win/oppBtn.png")}
                    className="elementBgImg"
                  />
                </button>
                <button className="oppBtn" onClick={handleClearAllValues}>
                  <label>Clear</label>
                  <img
                    src={require("../Assets/images/spin2win/oppBtn.png")}
                    className="elementBgImg"
                  />
                </button>
                <button
                  className={canBet ? "oppBtn" : "oppBtn disabled"}
                  onClick={handleBetPlacement}
                  disabled={!canBet}
                >
                  <label>Bet</label>
                  <img
                    src={require("../Assets/images/spin2win/oppBtn.png")}
                    className="elementBgImg"
                  />
                </button>
              </div>
            </div>
            <div className="col-4">
              <div className="timerWrapper">
                <label>{gameTime}</label>
                <label className="currentTime">{time}</label>
                <img
                  src={require("../Assets/images/spin2win/timer.png")}
                  className="elementBgImg"
                />
                <img
                  src={require("../Assets/images/spin2win/timerDuration.png")}
                  className="timerDuration"
                  style={{
                    clipPath: `inset(0px ${convertTo100Percent(
                      gameTime
                    )}% 0px 0px)`,
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer />
    </div>
  )
};


export default Spin2Win;
