import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { toastConfig } from "../../Globals/GlobalMetaData";

export function BetOpperation() {
  const [betStringArray, setBetStringArray] = useState([]);
  const [totalBetArr, setTotalBetArr] = useState({});
  const [totalBetAmount, setTotalBetAmount] = useState(0);
  const [selectedChip, setSelectedChip] = useState(1);

  // Handles and Prepares the TotalBetArray (just the bets)
  const handleSelectBet = (betItem) => {
    if (selectedChip > 0) {
      setTotalBetArr((prevTotalBetArr) => {
        const currentBetValue = prevTotalBetArr[betItem] || 0;
        const newBetValue = currentBetValue + selectedChip;

        const updatedBetArr = {
          ...prevTotalBetArr,
          [betItem]: newBetValue,
        }

        const betString = Object.entries(updatedBetArr).map(([key, value]) => `${key}Q${value}`)
        setBetStringArray(betString)

        return updatedBetArr;
      });
    } else {
      toast.error("Please Select a Chip!", toastConfig);
    }
  };

  // Place Bets on Even or Odd Numbers
  const handleOddEvenSelect = (param) => {
    setTotalBetArr((prevTotalBetArr) => {
      const newBets = { ...prevTotalBetArr };

      // Ensure to cover all numbers from 0 to 9, regardless of the previous state
      const numbersToBetOn = Array.from({ length: 10 }, (_, i) => i);

      numbersToBetOn.forEach((num) => {
        if (
          (param === "odd" && num % 2 !== 0) ||
          (param === "even" && num % 2 === 0)
        ) {
          const betItem = `N${num}`;
          newBets[betItem] = (newBets[betItem] || 0) + selectedChip;
        }
      });

      return newBets;
    });
  };

  // Handle Double Bets
  const handleDoubleBet = () => {
    setTotalBetArr((prevTotalBetArr) => {
      const newBets = {};
      for (const betItem in prevTotalBetArr) {
        if (prevTotalBetArr.hasOwnProperty(betItem)) {
          newBets[betItem] = prevTotalBetArr[betItem] * 2;
        }
      }
      return newBets;
    });
  };

  const handleTotalBetAmount = () => {
    let betAmount = 0;
    if (Object.keys(totalBetArr).length > 0) {
      for (const betItem in totalBetArr) {
        if (totalBetArr.hasOwnProperty(betItem)) {
          betAmount += totalBetArr[betItem];
        }
      }
      setTotalBetAmount(betAmount);
    }
  };

  useEffect(() => {
    handleTotalBetAmount();
  }, [totalBetArr]);

  // handle Clear All Bets
  const handleClearAllValues = () => {
    setTotalBetArr({});
    setTotalBetAmount(0);
  };

  return {
    selectedChip,
    setSelectedChip,
    betStringArray,
    totalBetArr,
    setTotalBetArr,
    totalBetAmount,
    handleSelectBet,
    handleDoubleBet,
    handleOddEvenSelect,
    handleClearAllValues,
  };
}
