import React, { useContext } from 'react'
import { DataContext } from '../../Context/DataContext'
import { UserOpperation } from './UserOpperation';
import screenfull from 'screenfull';

function Header({ inGame }) {
    const { userBalance, userName } = useContext(DataContext);
    const { printCheckboxRef } = UserOpperation();


    const fullscreen = () => {
        if (screenfull.isEnabled) {
            screenfull.toggle();
        } else {
            console.error("Fullscreen is not enabled or supported in this browser.");
        }
    }

    return (
        <>
            <div className='header'>
                <div className='container-fluid'>
                    <div className='row'>
                        <div className='col-auto'>
                            <div className='tabGroup'>
                                <button>
                                    Lobby
                                </button>
                                {inGame === "12card" && <button className='active'>
                                    JEETO JOKER
                                </button>}
                            </div>
                        </div>
                        <div className='col-auto mt-auto'>
                            <div className='utilGroup'>
                                <div className='d-flex align-items-center'>
                                    <input type="checkbox" ref={printCheckboxRef} />
                                    <label>P</label>
                                </div>
                                <div className='mx-2'>
                                    <input type="text" />
                                    <button className='claimBtn'>CLAIM</button>
                                </div>
                                <div className='d-flex align-items-center'>
                                    <input type="checkbox" />
                                    <label>AC</label>
                                </div>
                            </div>
                        </div>
                        <div className='col-auto ms-auto'>
                            <div className='d-flex align-items-center'>
                                <label className='userId'>Welcome, {userName}</label>
                                <div className='balanceGroup'>
                                    <label>BALANCE</label>
                                    <div className='balanceBox'>
                                        {userBalance}
                                    </div>
                                    <img src={require('../../Assets/images/header/balance_border.png')} />
                                </div>
                                <button className='exitButton' onClick={fullscreen}>
                                    <img src={require('../../Assets/images/header/minimize.png')} />
                                </button>
                                <button className='exitButton mx-2' >
                                    <img src={require('../../Assets/images/header/close.png')} />
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Header