let basePort = "3076"; // Default port

const getBaseUrl = () => `http://103.162.120.117:${basePort}/`;

let axiosInstance = axios.create({
  baseURL: getBaseUrl(),
  headers: { "Content-Type": "application/json" },
});

export const updatePort = (newPort) => {
  basePort = newPort; // Update the port dynamically
  axiosInstance = axios.create({
    baseURL: getBaseUrl(), // Reinitialize axios with the new baseURL
    headers: { "Content-Type": "application/json" },
  });
};

// Custom Axios Checker
export function checkIfAxios() {
  if (axiosInstance) {
    return true
  } else return false
}

//Game Data Function
export async function getTimeLeft() {
  return axiosInstance
    .get(`/gettime`)
    .then((res) => {
      const preparedData = {
        gameTime: res.data.timeleft,
        gameId: res.data.gameid,
        serverTime: res.data.serverclock,
        nextGameDate: res.data.today,
        nextGameTime: res.data.nextgametime,
      };
      // console.log(preparedData);
      return preparedData;
    })
    .catch((error) => {
      if (error) {
        getTimeLeft();
      }
    });
}

