// Get Advance Draw Game ID
export function getPrecedingParams(param) {
  const alphabetRange = "ABCDEFGHIJKLMN";
  const numberRange = 12;

  // Extract the letter and number parts from the input parameter
  const letterPart = param[0];
  const numberPart = parseInt(param.slice(1), 10);

  // Get the starting indexes for letter and number
  const startLetterIndex = alphabetRange.indexOf(letterPart);
  const startNumberIndex = numberPart;

  const result = [];

  // Calculate the total number of parameters
  const totalParams =
    alphabetRange.length * numberRange -
    (startLetterIndex * numberRange + startNumberIndex + 1);

  for (let i = 0; i < totalParams; i++) {
    const letterIndex = Math.floor(
      (startLetterIndex * numberRange + startNumberIndex + 1 + i) / numberRange
    );
    const numberIndex = (startNumberIndex + 1 + i) % numberRange;
    result.push(
      alphabetRange[letterIndex] + numberIndex.toString().padStart(2, "0")
    );
  }

  return result;
}

// Convert GameId To Time
export function convertGameIdToTime(param) {
  let startHour = 9;
  let startMinute = 0;

  let letter = param.charAt(0);
  let number = parseInt(param.slice(1), 10);

  let letterValue = letter.charCodeAt(0) - "A".charCodeAt(0);
  let totalIntervals = letterValue * 12 + number;
  let totalMinutes = totalIntervals * 5;

  let newHour = startHour + Math.floor((startMinute + totalMinutes) / 60);
  let newMinute = (startMinute + totalMinutes) % 60;

  return `${newHour.toString().padStart(2, "0")}:${newMinute
    .toString()
    .padStart(2, "0")}`;
}

//Disable Keyboard And Mouse Input
export function disableKeyboardMouse(bool) {
  const inputFields = document.querySelectorAll("input");
  if (bool) {
    // Disable mouse events
    document.addEventListener("mousedown", preventDefault, true);
    document.addEventListener("mouseup", preventDefault, true);
    document.addEventListener("click", preventDefault, true);
    document.addEventListener("contextmenu", preventDefault, true);

    // Disable keyboard events
    document.addEventListener("keydown", preventDefault, true);
    document.addEventListener("keypress", preventDefault, true);
    document.addEventListener("keyup", preventDefault, true);

    inputFields.forEach((item) => {
      item.disabled = true;
    });
  } else {
    // Enable mouse events
    document.removeEventListener("mousedown", preventDefault, true);
    document.removeEventListener("mouseup", preventDefault, true);
    document.removeEventListener("click", preventDefault, true);
    document.removeEventListener("contextmenu", preventDefault, true);

    // Enable keyboard events
    document.removeEventListener("keydown", preventDefault, true);
    document.removeEventListener("keypress", preventDefault, true);
    document.removeEventListener("keyup", preventDefault, true);

    inputFields.forEach((item) => {
      item.disabled = false;
    });
  }
  return bool;
}

function preventDefault(e) {
  e.preventDefault();
}

export function convertTo100Percent(val) {
  let max = 120;
  let percent = (val / max) * 100;
  return percent;
}

//Current Time Function
export function getCurrentTime(time = Date.now()) {
  const currentDate = new Date(time);

  currentDate.setMinutes(currentDate.getMinutes());

  const hours = currentDate.getHours().toString().padStart(2, "0");
  const minutes = currentDate.getMinutes().toString().padStart(2, "0");
  const seconds = currentDate.getSeconds().toString().padStart(2, "0");
  const period = hours >= 12 ? "PM" : "AM";

  const currentTime = `${hours > 12 ? hours - 12 : hours}:${minutes} ${period}`;

  return currentTime;
}

