import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { audioPlayer, updatePort } from '../Globals/GlobalFunctions';

function Lobby() {
    var settings = {
        dots: false,
        fade: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
    };

    const navigate = useNavigate();
    return (
        <>
            <style>
                {`
                .loboto{
                    overflow: hidden;
                }
            `}
            </style>
            <div className='container-fluid px-0'>
                <div className='gameWrapper loboto'>
                    <div className='lobbyWrapper'>
                        <div className='container'>
                            <div className='slider-container'>
                                <Slider {...settings}>
                                    <div className='wrapper'>
                                        <div className='imgWrapper'>
                                            <img src={require('../Assets/images/lobby/spin2win.png')} />
                                        </div>
                                        <div style={{ width: 'max-content', margin: 'auto' }}><h2 onClick={() => { updatePort("3025"); navigate("/games/spin2win"); audioPlayer(1, "KeyPress"); }}>Spin 2 Win</h2></div>
                                    </div>
                                    <div className='wrapper'>
                                        <div className='imgWrapper'>
                                            <img src={require('../Assets/images/spin2win/bg.png')} />
                                        </div>
                                        <div style={{ width: 'max-content', margin: 'auto' }}><h2 onClick={() => { updatePort("3078"); navigate("/games/12card"); audioPlayer(1, "KeyPress"); }}>12 Card</h2></div>
                                    </div>
                                </Slider>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Lobby