import React, { useContext, useEffect, useRef, useState } from "react";
import { DataContext } from "../../Context/DataContext";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { toastConfig } from "../../Globals/GlobalMetaData";
import {
  audioPlayer,
  cancelLastBet,
  disableKeyboardMouse,
  generateTicketByBarcode,
  getCurrentResult,
  getCurrentTime,
  getGameResult,
  getTimeLeft,
} from "../../Globals/GlobalFunctions";
import { getUserData } from "../../Globals/Auth";
import { BetOpperation } from "./BetOpperation";
import { cardArray } from "../Joker12Card";

export function UserOpperation() {
  const {
    userName,
    passWord,
    userId,
    setUserBalance,
    setUserId,
    setUserStatus,
    gameId,
    setGameId,
    setTicketData,
    lastBetBarCode,
    setLastBetBarCode,
    setNextGameDate,
    setNextGameTime,
    setServerTime,
  } = useContext(DataContext);
  const { handleClearAllValues } = BetOpperation();
  const [time, setTime] = useState(getCurrentTime());
  const [gameTime, setGameTime] = useState(99999);
  const timeRef = useRef(time);
  const [isDayEnd, setIsDayEnd] = useState(false);
  const [isTimerActive, setIsTimerActive] = useState(true);
  const [gameResult, setGameResult] = useState([]);
  const [lastGameResult, setLastGameResult] = useState([]);
  const [isTicketType, setIsTicketType] = useState("");
  const [canBet, setCanBet] = useState(true);
  const printCheckboxRef = useRef();
  const multiBetRef = useRef();
  const resultWheelRef = useRef();
  const resultWheelInnerRef = useRef();
  const [currentGameResult, setCurrentGameResult] = useState("");
  const [isSpin, setIsSpin] = useState(false);
  const [spinSpeed, setSpinSpeed] = useState(0);
  const [canLastReceipt, setCanLastReceipt] = useState(false);

  const toastIdNoBets = "nobts";
  const toastIdLastChance = "lstchnc";

  // Update User Balance Function
  async function updateUserData() {
    const data = await getUserData(userName, passWord);
    if (data) {
      setUserBalance(data.balance);
      setUserId(data.userid);
      setUserStatus(data.status);
    } else {
      toast.error("Error! Failed to load Balance!", toastConfig);
      setUserBalance(0);
    }
  }

  // Handle Ticket Print Function
  async function handleTicketPrint(barcode) {
    if (printCheckboxRef.current.checked) {
      setTicketData(null);
      const data = await generateTicketByBarcode(barcode, userId);
      if (data) {
        setTicketData(data);
        const res = await window.electronAPI.printFocusWindow();
        handleClearAllValues();
        if (multiBetRef.current.checked) {
          setTimeout(() => {
            setCanBet(true);
          }, 2000);
        }
      } else {
        // return null;
        toast.error("Error! Failed to load Ticket!", toastConfig);
      }
      // console.log(data)
    } else if (multiBetRef.current.checked) {
      setTimeout(() => {
        setCanBet(true);
      }, 2000);
    }
  }

  // Ticket Cancel Function
  async function handleTicketCancel() {
    const data = await cancelLastBet(lastBetBarCode);
    const toastId = toast.loading("Cancelling Bet, Please Wait!", {
      position: "top-center",
      hideProgressBar: false,
      theme: "colored",
    });
    if (data && data.message) {
      toast.update(toastId, {
        render: "Bet Cancelled Successfully!",
        type: "success",
        isLoading: false,
        autoClose: 2000,
      });
      setIsTicketType("CANCEL");
      handleTicketPrint();
      updateUserData();
      handleClearAllValues();
      // setCanCancelBet(false);
      //   barCodeSearchRef.current.disabled = false;
    } else {
      toast.update(toastId, {
        render: "Bet Cancellation Failed!",
        type: "error",
        isLoading: false,
        autoClose: 2000,
      });
      // toast.error("Bet Cancellation Failed!", toastConfig);
    }
  }

  // This is a simple Utility Function for Toggles to display a Toast
  function handleUtilityToggle(utility, toastMsg) {
    if (utility.current.checked) {
      toast.success(`${toastMsg} is now Enabled!`, toastConfig);
    } else {
      toast.error(`${toastMsg} is now Disabled!`, toastConfig);
    }
  }

  // Update Game Time
  useEffect(() => {
    const updateClock = () => {
      const currentTime = getCurrentTime();
      if (currentTime !== timeRef.current) {
        timeRef.current = currentTime;
        setTime(currentTime);
      }
    };
    const intervalId = setInterval(updateClock, 1000);
    // Initial update to prevent lag
    updateClock();
    return () => clearInterval(intervalId);
  }, []);

  // Update Game Result & Round Timer
  const updateGameTime = () => {
    // handleGameResults();
    updateUserData();
    getTimeLeft().then((data) => {
      setGameId(data.gameId);
      setNextGameDate(data.nextGameDate);
      setNextGameTime(data.nextGameTime);
      setServerTime(data.serverTime);
      if (data.gameTime < -10) {
        // Case: Game time is invalid (less than -1)
        setIsDayEnd(true);
      } else if (data.gameTime < 1 && !isTimerActive) {
        // Case: Timer is inactive and game time is less than 1
        setGameTime(0);
      } else if (data.gameTime <= 10 && isTimerActive && data.gameTime > 5) {
        // Case: Last 10 seconds of the game (but more than 5 seconds)
        setGameTime(10);
        setCanBet(false);
        if (!toast.isActive(toastIdNoBets)) {
          toast.error("No More Bets", {
            ...toastConfig,
            toastId: toastIdNoBets,
          });
          audioPlayer(1, "NoMoreBets");
        }
      } else if (data.gameTime <= 30 && isTimerActive && data.gameTime > 10) {
        // Case: Last 30 seconds of the game (but more than 10 seconds)
        if (!toast.isActive(toastIdLastChance)) {
          toast.error("Last Chance", {
            ...toastConfig,
            toastId: toastIdLastChance,
          });
          audioPlayer(1, "LastChance");
        }
      } else {
        // Default case: Update game time and reset day-end flag
        setGameTime(data.gameTime);
        setIsDayEnd(false);
      }
    });
  };

  const handlePreTimerReset = (gameName) => {
    handleCurrentGameResult(gameName);
    // setShowResult(true);
    // setReportTrigger(false);
    // handleGameResults();
    setLastBetBarCode("");
    setTicketData(null);
    // setReportTrigger(false);
  };

  // Timer Reset Function
  const handleTimerReset = () => {
    setIsTimerActive(true);
    // setShowResult(false);
    updateUserData();
    setCanBet(true);
    handleClearAllValues();
    disableKeyboardMouse(false);
    audioPlayer(1, "slide");
    toast.info("Place Your Bets", toastConfig);
    setTimeout(() => {
      audioPlayer(1, "PlaceYourBets");
    }, 500);
  };

  useEffect(() => {
    if (!isSpin) {
      handleGameResults();
    }
  }, [isSpin]);

  // Update All Game Result Function
  async function handleGameResults() {
    const currentDate = new Date().toISOString().split("T")[0];
    const data = await getGameResult(currentDate);
    setGameResult(data);
  }

  useEffect(() => {
    if (gameResult.length > 0) {
      const lastResultId = parseInt(
        gameResult[gameResult.length - 1]?.gameresult
      );
      const result = cardArray?.find((card) => card.ticketId === lastResultId);
      setLastGameResult({
        ...result,
        multiplier: gameResult[gameResult.length - 1].xresult,
      });
    }
  }, [gameResult]);

  //Current Game Result
  async function handleCurrentGameResult(gameName) {
    setIsSpin(true);
    spinLeWheel();
    const data = await getCurrentResult(gameId);
    if (data) {
      if (!data.result && data.id !== gameId) {
        setTimeout(handleCurrentGameResult, 300);
      } else {
        const resultInCard = cardArray.filter((item) => item.ticketId === parseInt(data.result))

        setCurrentGameResult({ ...resultInCard, multiplier: data.multiplier });
        setTimeout(() => {
          resultWheelRef.current.style.transition = "1s linear";
          if (gameName === "spin2win") {
            if (currentGameResult === "0") {
              resultWheelRef.current.style = "transform: rotate(252deg)";
            } else if (currentGameResult === "1") {
              resultWheelRef.current.style = "transform: rotate(216deg)";
            } else if (currentGameResult === "2") {
              resultWheelRef.current.style = "transform: rotate(180deg)";
            } else if (currentGameResult === "3") {
              resultWheelRef.current.style = "transform: rotate(144deg)";
            } else if (currentGameResult === "4") {
              resultWheelRef.current.style = "transform: rotate(108deg)";
            } else if (currentGameResult === "5") {
              resultWheelRef.current.style = "transform: rotate(72deg)";
            } else if (currentGameResult === "6") {
              resultWheelRef.current.style = "transform: rotate(36deg)";
            } else if (currentGameResult === "7") {
              resultWheelRef.current.style = "transform: rotate(0deg)";
            } else if (currentGameResult === "8") {
              resultWheelRef.current.style = "transform: rotate(324deg)";
            } else if (currentGameResult === "9") {
              resultWheelRef.current.style = "transform: rotate(288deg)";
            }
          } else if (gameName === "12card") {
            if (currentGameResult.name.substring(0, 1) === "K") {
              resultWheelRef.current.style = "transform: rotate(0deg)";
            } else if (currentGameResult.name.substring(0, 1) === "Q") {
              resultWheelRef.current.style = "transform: rotate(-30deg)";
            } else if (currentGameResult.name.substring(0, 1) === "J") {
              resultWheelRef.current.style = "transform: rotate(-60deg)";
            }

            if (currentGameResult.name.substring(1) === "H") {
              resultWheelInnerRef.current.style = "transform: rotate(0deg)"
            } else if (currentGameResult.name.substring(1) === "S") {
              resultWheelInnerRef.current.style = "transform: rotate(-30deg)"
            } else if (currentGameResult.name.substring(1) === "D") {
              resultWheelInnerRef.current.style = "transform: rotate(-60deg)"
            } else if (currentGameResult.name.substring(1) === "C") {
              resultWheelInnerRef.current.style = "transform: rotate(-90deg)"
            }
          }
        }, 5000);
        setIsSpin(false);
      }
    } else {
      setTimeout(() => {
        spinLeWheel();
      }, 5000);
    }
  }

  // Wheel Spin Function
  function spinLeWheel() {
    resultWheelRef.current.style = "transition: 5s ease-in-out";
    const currentRotation =
      resultWheelRef.current.style.transform.replace(/[^0-9.-]/g, "") || 0;
    const newRotation = parseFloat(currentRotation) + 10 * 360;
    resultWheelRef.current.style.transform = `rotate(${newRotation}deg)`;

    if (resultWheelInnerRef) {
      resultWheelInnerRef.current.style = "transition: 5s ease-in-out";
      const currentRotation =
        resultWheelInnerRef.current.style.transform.replace(/[^0-9.-]/g, "") || 0;
      const newRotation = parseFloat(currentRotation) - 10 * 360;
      resultWheelInnerRef.current.style.transform = `rotate(${newRotation}deg)`;
    }
  }

  return {
    time,
    gameTime,
    setGameTime,
    isTimerActive,
    setIsTimerActive,
    spinSpeed,
    isSpin,
    currentGameResult,
    gameResult,
    lastGameResult,
    isTicketType,
    setIsTicketType,
    canBet,
    setCanBet,
    printCheckboxRef,
    multiBetRef,
    resultWheelRef,
    resultWheelInnerRef,
    updateUserData,
    handleTicketPrint,
    handleTicketCancel,
    handleUtilityToggle,
    updateGameTime,
    handlePreTimerReset,
    handleTimerReset,
    handleCurrentGameResult,
    handleGameResults,
    canLastReceipt,
    setCanLastReceipt
  };
}
