import logo from './logo.svg';
import './App.css';
import Login from './Pages/Login';
import { HashRouter, Navigate, Route, Routes } from 'react-router-dom';
import Spin2Win from './Pages/Spin2Win';
import Lobby from './Pages/Lobby';
import Joker from './Pages/Joker';
import Joker12Card from './Pages/Joker12Card';
import { useEffect } from 'react';

function App() {
  return (
    <HashRouter>
      <Routes>
        <Route path='/login' element={<Login />} />
        <Route path='/lobby' element={<Lobby />} />

        {/* Path To Games */}
        <Route path="/games/spin2win" element={<Spin2Win />} />
        <Route path="/games/joker" element={<Joker />} />
        <Route path="/games/12card" element={<Joker12Card />} />

        <Route path="*" element={<Navigate to="/login" />} />
      </Routes>
    </HashRouter>
  );
}

export default App;
