import React, {
  useCallback,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import { toastConfig, verNo } from "../Globals/GlobalMetaData";
import {
  checkMacId,
  checkMacIdByMac,
  checkMacIdExist,
  login,
  updateMacId,
} from "../Globals/Auth";
import { audioPlayer } from '../Globals/GlobalFunctions'
import { useNavigate } from "react-router-dom";
import { DataContext } from "../Context/DataContext";
import { FadeLoader } from "react-spinners";
import { toast } from "react-toastify";

function Login() {
  const { setUserName, setPassWord, setUserId, userMacId, setUserMacId, setUserBalance } =
    useContext(DataContext);
  const [isLoading, setIsLoading] = useState(false); // Will always True
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [userIdForm, setUserIdForm] = useState("");
  const [error, setError] = useState("");
  const [isMacRegistered, setIsMacRegistered] = useState(false);
  const [isMacId, setIsMacId] = useState(true); // Will Stay True
  const passwordInput = useRef();

  const navigate = useNavigate();

  const userStore = localStorage.getItem("username");
  const passStore = localStorage.getItem("password");

  // Loader
  // useEffect(() => {
  //     setTimeout(() => {
  //         setIsLoading(false)
  //     }, 2500)
  // })

  // Deriving User Mac ID
  useEffect(() => {
    async function getMacAddress() {
      const res = await window.electronAPI.getMacAddress();
      // const res = await systemServGet('getmac')
      if (res) {
        setUserMacId(res);
        if (userMacId) {
          setIsLoading(false);
          // Remove the Following Comment & Delete the setIsLoading To Start Registration Function LOL
          // checkMacIdAuth();
          setTimeout(() => {
            if (passwordInput.current) {
              passwordInput.current.focus();
            }
          }, 200);
        }
      }
    }
    // getMacAddress();
  }, [userMacId]);

  // Check Mac ID
  async function checkMacIdAuth() {
    const data = await checkMacIdExist(userStore);
    // const data = await checkMacIdByMac(userStore, userMacId);
    // const data = await window.electronAPI.checkHardMac();
    if (data && data === userMacId) {
      setIsMacId(true);
      setIsLoading(false);
      setTimeout(() => {
        if (passwordInput.current) {
          passwordInput.current.focus();
        }
      }, 200);
    } else {
      setIsMacId(false);
      setIsLoading(false);
    }
  }

  // Update Mac ID
  async function handleMacIdAuth() {
    if (!username.trim()) {
      setError("Username is Missing");
      toast.error("Username is Missing!", toastConfig);
      setTimeout(setError, 2000, "");
    } else if (!userMacId.trim()) {
      checkMacIdAuth();
    } else {
      const data = await updateMacId(username, userMacId.toString());
      if (data && data.message.includes("updated mac id")) {
        setIsMacRegistered(true);
        setTimeout(() => {
          setIsMacId(true);
        }, 2000);
      }
    }
  }

  // Reset Mac ID
  async function resetMacId() {
    const data = await login("9903", password, userMacId.toString());
    if (data) {
      navigate("/");
      localStorage.clear();
    }
  }

  // Handle Remember Credentials
  useEffect(() => {
    if (userStore) {
      setUsername(userStore);
      setPassword(passStore);
    }
  }, []);

  // Handle Login
  async function checkLogin() {
    audioPlayer(1, "KeyPress");
    if ((!username.trim() && !password.trim()) || !password.trim()) {
      setError("Please enter correct Username & Password!");
      toast.error("Please enter correct Username & Password!", toastConfig);
      setTimeout(setError, 2000, "");
    } else if (password === "9903") {
      resetMacId();
    } else {
      const data = await login(username, password, userMacId.toString());
      if (data && data?.message?.includes("badmac")) {
        setError("Contact Office: Mac ID Mismatch");
        toast.error("Contact Office: Mac ID Mismatch", toastConfig);
        setTimeout(setError, 2000, "");
      } else if (data) {
        setUserName(username);
        setPassWord(password);
        setUserBalance(data.CLIENTBALANCE);
        localStorage.setItem("username", username);
        localStorage.setItem("password", password);
        // setUserId(userIdForm)
        navigate("/games/12card");
      } else {
        setError("Network Error! Please Try Again!");
        toast.error("Network Error! Please Try Again!", toastConfig);
        setTimeout(setError, 2000, "");
        setPassword("");
      }
    }
  }

  const handleKeyPress = useCallback(
    (event) => {
      switch (event.key) {
        case " ":
        case "Enter":
          if (!isMacId) {
            handleMacIdAuth();
            break;
          } else {
            checkLogin();
            break;
          }
        case "Escape":
          audioPlayer(1, "KeyPress");
          window.electronAPI.quitApp();
          break;
        default:
          break;
      }
    },
    [handleMacIdAuth, checkLogin]
  );

  useEffect(() => {
    document.addEventListener("keydown", handleKeyPress);

    return () => {
      document.removeEventListener("keydown", handleKeyPress);
    };
  }, [handleKeyPress]);

  const preventCopyPaste = (e) => {
    e.preventDefault();
  };

  return isLoading ? (
    <div className="loader">
      <FadeLoader color="#454545" />
    </div>
  ) : (
    <main className="loginWrapper">
      <div className="position-absolute top-0 col-12 d-flex justify-content-between px-2">
        <div>
          <label>Ver {verNo}</label>
        </div>
        <div>
          <label>
            {userMacId
              ? userMacId.replace(/:/g, "").toUpperCase()
              : "No Mac Found"}
          </label>
        </div>
      </div>
      <div className="container h-100">
        <div className="row h-100">
          <div className="col-6 my-auto">
            <div className="wrapper p-2">
              {!isMacId ? (
                <div className="loginMotherWrapper">
                  <div className="formWrapper">
                    <div className="loginHeading">
                      {isMacRegistered
                        ? "REGISTERED SUCCESSFULLY"
                        : "TERMINAL REGISTRATION"}
                    </div>
                    <div className="col-12">
                      <div className="d-flex justify-content-center">
                        <div className="col-3 me-2">
                          <label>Username: </label>
                        </div>
                        <div>
                          <input
                            type="text"
                            className="loginInput"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            onCopy={(e) => preventCopyPaste(e)}
                            onPaste={(e) => preventCopyPaste(e)}
                            style={{ width: "150px" }}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-6 mt-4 text-center">
                      <button
                        id="loginBtn"
                        className="loginButton"
                        onClick={handleMacIdAuth}
                      >
                        Submit
                      </button>
                    </div>
                  </div>
                </div>
              ) : (
                <div className={"loginMotherWrapper"}>
                  <div className="formWrapper">
                    {/* <div className='col-12 '>
                                <div className='d-flex justify-content-center'>
                                    <div className='col-1 me-2'>
                                        <label>UserID</label>
                                    </div>
                                    <div>
                                        <input type="text" className="loginInput" value={userIdForm} onChange={(e) => setUserIdForm(e.target.value)} />
                                    </div>
                                </div>
                            </div> */}
                    <div className="col-12 mt-2">
                      <div className="d-flex justify-content-center">
                        <div className="col-3 me-2">
                          <label>Username:</label>
                        </div>
                        <div>
                          <input
                            type="text"
                            className="loginInput"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            onCopy={(e) => preventCopyPaste(e)}
                            onPaste={(e) => preventCopyPaste(e)}
                            disabled={userStore ? true : false}
                            style={{ cursor: userStore ? "none" : "default" }}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-12 mt-2">
                      <div className="d-flex justify-content-center">
                        <div className="col-3 me-2">
                          <label>Password:</label>
                        </div>
                        <div>
                          <input
                            type="password"
                            className="loginInput"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            onCopy={(e) => preventCopyPaste(e)}
                            onPaste={(e) => preventCopyPaste(e)}
                            ref={passwordInput}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-6 mt-4 text-center">
                      <button
                        className="loginButton"
                        id="loginBtn"
                        onClick={checkLogin}
                      >
                        Login
                      </button>
                    </div>
                    {error ? (
                      <div className="col-12 text-center mt-3">
                        <label>{error ? error : ""}</label>
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                </div>
              )}
            </div>
          </div>
          <div className="col-6 my-auto">
            <div className="loginRules">
              <img src={require("../Assets/images/login/Login.png")} />
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}

export default Login;
