import React, { useState, useEffect } from 'react';

const ImageSlider = ({ images, className, transTime }) => {
    const [currentIndex, setCurrentIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentIndex((prevIndex) => (prevIndex + 1) % images.length);
        }, transTime);

        return () => clearInterval(interval);
    }, [images.length]);

    return (
        <img
            src={images[currentIndex].url}
            alt={`Slide ${currentIndex}`}
            className={className}
        />
    );
};

export default ImageSlider;

// Vanilla JS Function of the above Logic.
export function createImageSlider(images, container, imgClass) {
    const sliderContainer = document.querySelector(`.${container}`);
    let currentIndex = 0;

    function showImage(index) {
        sliderContainer.innerHTML = '';
        const imgElement = document.createElement('img');
        imgElement.src = images[index].url;
        imgElement.alt = `Slide ${index}`;
        imgElement.classList.add = `${imgClass}`
        sliderContainer.appendChild(imgElement);
    }

    showImage(currentIndex);

    setInterval(() => {
        currentIndex = (currentIndex + 1) % images.length;
        showImage(currentIndex);
    }, 2000);
}
